  padding: 0;
}

ul, ol, li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

img {
  vertical-align: top;
}

header {
  position: fixed;
  left: 3%;
  top: 5%;
  z-index: 999;
}

header .head {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

header .head.color .logo span {
  color: #000;
}

header .head.color .menu a {
  color: #000;
}

header .head.none .logo span {
  display: none;
}

header .head.none .menu a {
  display: none;
}

header .head .logo {
  display: flex;
  transition: .5s;
  cursor: pointer;
}

header .head .logo.active span:nth-of-type(3) {
  margin-left: 7%;
  -webkit-animation: p1 2s forwards;
          animation: p1 2s forwards;
}

@-webkit-keyframes p1 {
  0% {
    -webkit-transform: translateX(-800%);
            transform: translateX(-800%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes p1 {
  0% {
    -webkit-transform: translateX(-800%);
            transform: translateX(-800%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

header .head .logo.active span:nth-of-type(5) {
  -webkit-animation: p2 2s forwards;
          animation: p2 2s forwards;
}

@-webkit-keyframes p2 {
  0% {
    -webkit-transform: translateX(-1000%);
            transform: translateX(-1000%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes p2 {
  0% {
    -webkit-transform: translateX(-1000%);
            transform: translateX(-1000%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

header .head .logo.active .hide {
  display: block;
  -webkit-animation: hide 2s forwards;
          animation: hide 2s forwards;
}

@-webkit-keyframes hide {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes hide {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

header .head .logo span {
  font-size: 2rem;
  display: block;
  color: #fff;
}

header .head .logo .show {
  font-weight: normal;
  z-index: 1;
}

header .head .logo .hide {
  font-weight: 300;
  display: none;
}

header .head .menu {
  line-height: 3rem;
}

header .head .menu a {
  display: block;
  font-size: 2rem;
  margin: 3% 0;
  transition: .2s;
  overflow: hidden;
}

header .head .menu a::after {
  content: '';
  display: block;
  width: 90%;
  height: 2px;
  background-color: #dcf738;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
  transition: .6s;
}

header .head .menu a:hover {
  color: #dcf738;
}

header .head .menu a:hover::after {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}

header .arrow {
  position: fixed;
  right: -5%;
  bottom: 5%;
  width: 30%;
}

header .arrow .arr {
  position: relative;
  width: 10%;
  margin: 10% auto;
}

header .arrow .arr img {
  width: 100%;
}

header .arrow .arr .round {
  width: 350%;
  position: absolute;
  left: calc(50% - 175%);
  top: -165%;
  -webkit-animation: around 100000s infinite;
          animation: around 100000s infinite;
}

@-webkit-keyframes around {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(10000000deg);
            transform: rotate(10000000deg);
  }
}

@keyframes around {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(10000000deg);
            transform: rotate(10000000deg);
  }
}

header .arrow .arr .round img {
  width: 100%;
}

header .arrow.color {
  display: none;
}

